import React from 'react'
import PropTypes from 'prop-types' 
// import { Link } from 'react-router-dom';


export default function Navbar(props) {
  return (
  
      <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
  <div className="container-fluid">
    <a className="navbar-brand" href='/'>{props.title}</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <a className="nav-link active" aria-current="page" href="/">home</a>
        </li>
        <li className="nav-item">
          <a className="nav-link" href ='/'>{props.aboutText}</a>
        </li>

        {/* <li className="nav-item">
          <Link className="nav-link active" aria-current="page" to="/">home</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to ="/about">{props.aboutText}</Link>
        </li> */}
     
      </ul>

      <form className="d-flex" role="search">
        <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"/>
        <button className="btn btn-outline-success" type="submit">Search</button>
      </form>

      <div className="d-flex  ">
        <div className="bg-primary rounded mx-2 mt-2"onClick={()=>{props.toggleMode('primary')}} style={{height:'30px',width:'30px'}}></div>
      
      
        <div className="bg-danger rounded mx-2 mt-2"onClick={()=>{props.toggleMode('danger')}} style={{height:'30px',width:'30px'}}></div>
      
      
        <div className="bg-success rounded mx-2 mt-2"onClick={()=>{props.toggleMode('success')}} style={{height:'30px',width:'30px'}}></div>

        <div className="bg-warning rounded mx-2 mt-2"onClick={()=>{props.toggleMode('warning')}} style={{height:'30px',width:'30px'}}></div>

        <div className="bg-light rounded mx-2 mt-2"onClick={()=>{props.toggleMode('light')}} style={{height:'30px',width:'30px',border:'1px solid black'}}></div>

        <div className="bg-dark rounded mx-2 mt-2"onClick={()=>{props.toggleMode('dark')}} style={{height:'30px',width:'30px',border:'1px solid white'}}></div>

        
      </div>




      {/* <div className={`form-check form-switch  my-2 mx-3 text-${props.mode==='light'?'dark':'light'}`}>
  <input className="form-check-input" onClick={()=>{props.toggleMode(null)}}type="checkbox"  id="flexSwitchCheckDefault"/>
  <label className="form-check-label" htmlFor="flexSwitchCheckDefault">Enable DarkMode</label>
</div> */}
    </div>
  </div>
</nav>
    
  )
}

  

Navbar.propTypes = {
   title: PropTypes.string,
   aboutText: PropTypes.string,
   Home:PropTypes.string
   
};
Navbar.defaultProps = {
  title:'set title here',
  aboutText:'about'
};